function treeEncounter(goTo) {
  const sceneA = new Dialog(emptyImg, "I AM GROOT");

  const sceneB = new Dialog(emptyImg, "Vous rcoltez du bois");

  const answerA1 = new Answer({ text: "Rcolter du bois", goTo: sceneB });
  sceneA.addAnswer(answerA1);

  const answerA2 = new Answer({
    text: "S'en aller",
    shouldExit: true,
  });
  sceneA.addAnswer(answerA2);

  const answerB1 = new Answer({
    text: "S'en aller",
    shouldExit: true,
    getItem: true,
    itemProps: { name: "wood", fill: "url(#treeLeef2)" },
  });
  sceneB.addAnswer(answerB1);

  //console.log("answerB1");

  if (goTo === undefined) {
    goTo = sceneA;
  }

  return goTo;
}
